.main-container {
    background-color: #eae7de;
    overflow: hidden;

    & > div.title-container {
        width: 1000px;
        margin: auto;
        padding: 10px 16px;
    }
    & > div.middle-container {
        background-color: orange;
        margin: auto;
        height: 400px;
        padding: 10px 16px;
        position: relative;

        & >.middle-description {
            width: 1000px;
            margin: auto;
            padding: 0 16px;
        }
        
        & > .dentist-container {
            position: absolute;
            left: 50%;
            bottom: -5rem;

            & > .dentist {
                width: 650px; 
            }
        }
    }
}
.posts-container {
    width: 90%;
    max-width: 10000px;
    margin: auto;

    & > .posts {
        display: flex;
        flex-wrap: wrap;
        width: 100%;

        & > .post {
            padding: 10px;
            border: 1px solid black;
            height: 300px;
            width: 25%;
            @media screen and (max-width: 1360px)  {
                width: 33.3%;
            } 
            @media screen and (max-width: 900px) {
                width: 50%;
            }
            @media screen and (max-width: 720px) {
                width: 100%;
            }

        }
    }
}