.header-container {
    background-color: #eae7de;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #051619;

    padding: 30px 100px;

    & > div.left-component {
        font-size: 18px;
        font-weight: 700;
    }
    & > div.center-component {
        display: flex;
        justify-content: center;

        & > .logo {
            width: 226px;
        }

    }
    & > div.right-component {
        display: flex;
        justify-content: center;
        align-items: center;
        
         & > div.icon-container {
            border: 1px solid black;
            padding: 6px;
            display: flex;
            justify-content: center;
        }
        
    }
}